<template>
    <div class="appBox">
        <div class="cooBg">
            <div class="cooBody">
                <p class="art">项目方可以申请接入自己的代币到本平台，只需要很短时间和成本，即可使用自己的代币作为筹码，就相当于给自己的代币开了模式，然后让社区领导人来玩红包接龙，这将大大提升代币的流通性和价格，社区领导人也乐于接受，并且获利巨大。</p>
                <p class="art1">注：目前仅支持erc20代币的上线申请</p>
                <div class="form">
                    <label>币种全称</label>
                    <input type="text" v-model="form.name" placeholder="如：比特币全称为'Bitcoin'" />
                    <label>币种简称</label>
                    <input type="text" v-model="form.name_lessen" placeholder="如：比特币简称为'BTC'" />
                    <label>代币智能合约地址</label>
                    <input type="text" v-model="form.contract" placeholder="合约地址" />
                    <label>代币logo图（80x80尺寸）</label>
                    <van-uploader
                            v-model="fileList"
                            multiple
                            :max-count="1"
                    />
                    <label>您的邮箱</label>
                    <input type="text" v-model="form.email" placeholder="代币上线成功后，将通过邮件通知您" />
                </div>
                <p class="art">申请上线erc20代币需要200usdt押金，当此币种在本平台的持币玩家达到50人时，即退还这200usdt押金。</p>
                <button @click="submitForm" class="verify">提交</button>
            </div>
        </div>
    </div>
</template>

<script>
    import { cooperation } from "@/api/index"
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { Uploader } from "vant"
    import 'vant/lib/uploader/style'
    import { isEmail } from "vant/es/utils/validate/email";
    export default {
        name: "Cooperation",
        components: {
            [Uploader.name]: Uploader,
        },
        data()
        {
            return {
                fileList:[],
                form:{
                    name:'',
                    name_lessen:'',
                    contract:'',
                    email:''
                }
            }
        },
        mounted()
        {

        },
        methods:{

            submitForm()
            {
                if (!this.form.name)
                {
                    Toast('请填写币种名称')
                    return;
                }
                if (!this.form.name_lessen)
                {
                    Toast('请填写币种简称')
                    return;
                }
                if (!this.form.contract)
                {
                    Toast('请填写合约地址')
                    return;
                }
                if (this.fileList.length <= 0)
                {
                    Toast('请上传币种Logo')
                    return
                }
                if (!this.form.email)
                {
                    Toast('请填写邮箱地址')
                    return;
                }
                if (!isEmail(this.form.email))
                {
                    Toast('请填写正确邮箱')
                    return
                }


                let param = new FormData() // 创建form对象
                param.append('file', this.fileList[0].file, this.fileList[0].file.name) // 通过a
                param.append('name', this.form.name) // 通过a
                param.append('name_lessen', this.form.name_lessen) // 通过a
                param.append('contract', this.form.contract) // 通过a
                param.append('email', this.form.email) // 通过a
                cooperation(param).then(res => {

                    Toast('申请成功请耐心等待')
                     this.$router.go(-1)
                })

            }
        }
    }
</script>

<style scoped>

    @import "../../static/css/cooperation.css";

</style>



